import React, { useState } from 'react';
import validator from 'validator';
import { COND } from '../util/const';
import { positiveInt } from '../util/valid';
import { download } from '../util/download';
import {
  PageHeader,
  Card,
  Col,
  Row,
  Icon,
  Typography,
  Form,
  Input,
  Button,
  Select,
  Layout,
  Statistic,
  notification,
  Modal
} from 'antd';
const { Text } = Typography;
const { Option } = Select;
const { confirm } = Modal;

const ROUTE = {
  CTRL_PANEL: 'CTRL_PANEL',
  NEW_GAME: 'NEW_GAME',
  RESUME: 'RESUME'
};

const items = [
  {
    label: '人數',
    id: 'size',
    initialValue: '20',
    rules: [
      {
        required: true,
        message: '必填'
      },
      {
        validator(rule, value, callback) {
          if (
            validator.isInt(value) &&
            parseInt(value) > 0 &&
            parseInt(value) % 2 === 0
          ) {
            callback();
          }
          callback('請填正偶數');
        }
      }
    ]
  },
  {
    label: '模式',
    id: 'cond',
    initialValue: 'NME',
    rules: [
      {
        required: true,
        message: '必填'
      }
    ]
  },
  {
    label: 'TP',
    id: 'dtp',
    initialValue: '2',
    rules: positiveInt
  },
  {
    label: 'A',
    id: 'a',
    initialValue: '100',
    rules: positiveInt
  },
  {
    label: 'B',
    id: 'b',
    initialValue: '5',
    rules: positiveInt
  },
  {
    label: 'C',
    id: 'c',
    initialValue: '1',
    rules: positiveInt
  },
  {
    label: '回合',
    id: 'round',
    initialValue: '1',
    rules: positiveInt
  }
];

const Admin = ({ form, tpd }) => {
  const { getFieldDecorator, getFieldValue, validateFields } = form;
  const [route, setRoute] = useState(ROUTE.CTRL_PANEL);
  const [creating, setCreating] = useState(false);

  const newGame = () => {
    setCreating(true);
    validateFields((err, values) => {
      if (err) return;
      console.log(values);
      tpd.socket.emit('newGame', values, err => {
        setCreating(false);
        if (err) {
          return notification.open({
            message: '遊戲建立失敗',
            description: err
          });
        }
        setRoute(ROUTE.RESUME);
      });
    });
  };

  console.log(tpd.game);

  return (
    <React.Fragment>
      {route === ROUTE.CTRL_PANEL && (
        <React.Fragment>
          <PageHeader title="控制台" />
          <Row gutter={16} style={{ padding: '12px 24px 24px 24px' }}>
            <Col span={12}>
              <Card onClick={() => setRoute(ROUTE.NEW_GAME)}>
                <p>
                  <Icon type="plus" />
                </p>
                <p>
                  <Text strong>新遊戲</Text>
                </p>
              </Card>
            </Col>
            <Col
              span={12}
              onClick={() => {
                if (tpd.game.player.max === 0) {
                  return notification.open({
                    message: '沒有遊戲正在進行',
                    description: '點擊「新遊戲」開始新遊戲。'
                  });
                }
                setRoute(ROUTE.RESUME);
              }}
            >
              <Card>
                <p>
                  <Icon type="caret-right" />
                </p>
                <p>
                  <Text strong>返回遊戲</Text>
                </p>
              </Card>
            </Col>
          </Row>
        </React.Fragment>
      )}
      {route === ROUTE.NEW_GAME && (
        <React.Fragment>
          <PageHeader
            title="新遊戲"
            onBack={() => setRoute(ROUTE.CTRL_PANEL)}
          />
          <Form
            onSubmit={e => {
              e.preventDefault();

              if (tpd.game.player.max > 0) {
                return confirm({
                  title: '新遊戲將覆蓋正在進行的遊戲',
                  content: '確定要繼續嗎？',
                  onOk: newGame
                });
              }
              newGame();
            }}
            style={{ padding: '12px 24px 24px 24px' }}
          >
            <Row gutter={16}>
              {items.map(({ label, id, initialValue, rules }, index) => (
                <Col key={index} span={8}>
                  <Form.Item label={label}>
                    {index === 1
                      ? getFieldDecorator(id, { initialValue, rules })(
                          <Select>
                            <Option value="NME">NME</Option>
                            <Option value="ME">ME</Option>
                          </Select>
                        )
                      : index === 2 && getFieldValue('cond') === COND.ME
                      ? '玩家決定'
                      : getFieldDecorator(id, { initialValue, rules })(
                          <Input />
                        )}
                  </Form.Item>
                </Col>
              ))}
            </Row>
            <Form.Item>
              <Button
                size="large"
                block
                type="primary"
                htmlType="submit"
                disabled={creating}
              >
                遊戲開始
              </Button>
            </Form.Item>
          </Form>
        </React.Fragment>
      )}
      {route === ROUTE.RESUME && (
        <React.Fragment>
          <PageHeader
            title="遊戲進行中"
            onBack={() => setRoute(ROUTE.CTRL_PANEL)}
          />
          <Layout style={{ padding: '12px 24px 24px 24px' }}>
            <Row gutter={16}>
              <Col span={8}>
                <Statistic title="人數" value={tpd.game.player.max} />
              </Col>
              <Col span={8}>
                <Statistic title="模式" value={tpd.game.cond} />
              </Col>
              <Col span={8}>
                <Statistic
                  title="TP"
                  value={
                    tpd.game.cond === COND.NME ? tpd.game.param.dtp : '玩家決定'
                  }
                />
              </Col>
              <Col span={8} style={{ marginTop: 32 }}>
                <Statistic title="A" value={tpd.game.param.a} />
              </Col>
              <Col span={8} style={{ marginTop: 32 }}>
                <Statistic title="B" value={tpd.game.param.b} />
              </Col>
              <Col span={8} style={{ marginTop: 32 }}>
                <Statistic title="C" value={tpd.game.param.c} />
              </Col>
              <Col span={8} style={{ marginTop: 32 }}>
                <Statistic title="回合" value={tpd.game.round} />
              </Col>
              <Col span={8} style={{ marginTop: 32 }}>
                <Statistic title="連線人數" value={tpd.game.player.connected} />
              </Col>
              <Col span={8} style={{ marginTop: 32 }}>
                <Statistic title="完成人數" value={tpd.game.player.done} />
              </Col>
            </Row>
            <Row gutter={16}>
              <Col span={12} style={{ marginTop: 24 }}>
                <Button
                  type="primary"
                  block
                  icon="forward"
                  size="large"
                  onClick={() => {
                    if (tpd.game.player.max !== tpd.game.player.done) {
                      return confirm({
                        title: '仍有玩家尚未完成交易',
                        content: '確定要繼續嗎？',
                        onOk() {
                          tpd.socket.emit('newRound');
                        }
                      });
                    }
                    tpd.socket.emit('newRound');
                  }}
                >
                  新回合
                </Button>
              </Col>
              <Col span={12} style={{ marginTop: 24 }}>
                <Button
                  type="danger"
                  icon="delete"
                  block
                  size="large"
                  onClick={() => {
                    confirm({
                      title: '將清空所有玩家的收益與回合',
                      content: '確定要繼續嗎？',
                      onOk() {
                        tpd.socket.emit('resetProfitRound');
                      }
                    });
                  }}
                >
                  清空收益回合
                </Button>
              </Col>
              <Col span={24} style={{ marginTop: 16 }}>
                <Button
                  icon="download"
                  block
                  size="large"
                  onClick={() =>
                    tpd.socket.emit('download', 'user', data =>
                      download('玩家資訊.csv', data)
                    )
                  }
                >
                  下載玩家資訊.csv
                </Button>
              </Col>
              <Col span={24} style={{ marginTop: 16 }}>
                <Button
                  icon="download"
                  block
                  size="large"
                  onClick={() =>
                    tpd.socket.emit('download', 'log', data =>
                      download('交易紀錄.csv', data)
                    )
                  }
                >
                  下載交易紀錄.csv
                </Button>
              </Col>
            </Row>
          </Layout>
        </React.Fragment>
      )}
    </React.Fragment>
  );
};

export default Form.create({ name: 'admin' })(Admin);
