import React from 'react';
import { nonNegativeInt } from '../util/valid';
import { COND, ROLE, PROMO } from '../util/const';
import {
  PageHeader,
  Icon,
  Typography,
  Form,
  Input,
  Button,
  Descriptions,
  Layout,
  Result,
  Modal
} from 'antd';
const { Title, Text } = Typography;
const { confirm } = Modal;

const Manufacturer = ({ form, tpd }) => {
  const { getFieldDecorator, validateFields } = form;

  return (
    <React.Fragment>
      <PageHeader title={tpd.role} subTitle={tpd.info.phase} />
      <Layout style={{ padding: '12px 24px 24px 24px' }}>
        {tpd.info.phase === 4 && (
          <Layout style={{ marginBottom: 19, textAlign: 'center' }}>
            <Title>{`${tpd.profit[tpd.profit.length - 1]} ED`}</Title>
            <Text>
              Profits you earn in{' '}
              <Text strong>{`period ${tpd.game.round}`}</Text>
            </Text>
          </Layout>
        )}
        <Descriptions bordered size="middle" style={{ marginBottom: 36 }}>
          <Descriptions.Item label="Production Cost">
            {tpd.game.param.c}
          </Descriptions.Item>
          {tpd.info.phase >= 2 && (
            <Descriptions.Item label="Wholesale Price">
              {tpd.info.pm}
            </Descriptions.Item>
          )}
          {tpd.info.phase >= (tpd.game.cond === COND.NME ? 1 : 2) && (
            <Descriptions.Item label="Trade Promotion Discount">
              {tpd.game.cond === COND.NME ? tpd.game.param.dtp : tpd.info.tp}
            </Descriptions.Item>
          )}
          {tpd.info.phase >= 3 && (
            <Descriptions.Item label="Trade Promotion Allocation">
              {tpd.info.promo}
            </Descriptions.Item>
          )}
          {tpd.info.phase === 4 && (
            <Descriptions.Item label="Order Quantity">
              {tpd.info.qm}
            </Descriptions.Item>
          )}
          {tpd.info.phase === 4 && (
            <Descriptions.Item label="Retail Price">
              {tpd.info.pr}
            </Descriptions.Item>
          )}
          {tpd.info.phase === 4 && (
            <Descriptions.Item label="Consumer Demands">
              {tpd.info.qr}
            </Descriptions.Item>
          )}
        </Descriptions>
        {tpd.role === ROLE.MANUFACTURER && tpd.info.phase === 1 && (
          <Form
            onSubmit={e => {
              e.preventDefault();
              validateFields((err, values) => {
                if (err) return;
                confirm({
                  title: `Do you wish to set wholesale price to ${values.pm}${
                    tpd.game.cond === COND.ME
                      ? ' and trade promotion discount to ' + values.tp
                      : ''
                  }?`,
                  onOk() {
                    tpd.socket.emit('setPmTp', {
                      pm: parseInt(values.pm),
                      tp:
                        tpd.game.cond === COND.ME
                          ? parseInt(values.tp)
                          : parseInt(tpd.game.param.dtp)
                    });
                  }
                });
              });
            }}
          >
            <Form.Item label="Wholesale Price">
              {getFieldDecorator('pm', { rules: nonNegativeInt })(
                <Input size="large" />
              )}
            </Form.Item>
            {tpd.game.cond === COND.ME && (
              <Form.Item label="Trade Promotion Discount">
                {getFieldDecorator('tp', { rules: nonNegativeInt })(
                  <Input size="large" />
                )}
              </Form.Item>
            )}
            <Form.Item>
              <Button size="large" block type="primary" htmlType="submit">
                Submit
              </Button>
            </Form.Item>
          </Form>
        )}
        {tpd.role === ROLE.RETAILER && tpd.info.phase === 1 && (
          <Result
            icon={<Icon type="loading" style={{ fontSize: 72 }} spin />}
            title="Hold on"
            subTitle={`Manufacturer is deciding the wholesale price${
              tpd.game.cond === COND.ME ? ' and trade promotion discount' : ''
            }.`}
          />
        )}
        {tpd.role === tpd.info.priority && tpd.info.phase === 2 && (
          <React.Fragment>
            <Title level={3} style={{ marginBottom: 16 }}>
              Please decide trade promotion allocation.
            </Title>
            <Button
              size="large"
              block
              type="primary"
              style={{ marginBottom: 12 }}
              onClick={() => {
                confirm({
                  title:
                    'Do you wish to set trade promotion allocation to off-invoices?',
                  onOk() {
                    tpd.socket.emit('setPromo', PROMO.OFFINVOICES);
                  }
                });
              }}
            >
              Off-invoices
            </Button>
            <Button
              size="large"
              block
              type="primary"
              onClick={() => {
                confirm({
                  title:
                    'Do you wish to set trade promotion allocation to scan-backs?',
                  onOk() {
                    tpd.socket.emit('setPromo', PROMO.SCANBACKS);
                  }
                });
              }}
            >
              Scan-backs
            </Button>
          </React.Fragment>
        )}
        {tpd.role !== tpd.info.priority && tpd.info.phase === 2 && (
          <Result
            icon={<Icon type="loading" style={{ fontSize: 72 }} spin />}
            title="Hold on"
            subTitle={`${
              tpd.info.priority === ROLE.MANUFACTURER
                ? 'Manufacturer'
                : 'Retailer'
            } is deciding the trade promotion allocation.`}
          />
        )}
        {tpd.role === ROLE.RETAILER && tpd.info.phase === 3 && (
          <Form
            onSubmit={e => {
              e.preventDefault();
              validateFields((err, values) => {
                if (err) return;
                confirm({
                  title: `Do you wish to set retail price to ${values.pr} and order quantity to ${values.qm}?`,
                  onOk() {
                    tpd.socket.emit('setQmPr', {
                      qm: parseInt(values.qm),
                      pr: parseInt(values.pr)
                    });
                  }
                });
              });
            }}
          >
            <Form.Item label="Retail Price">
              {getFieldDecorator('pr', { rules: nonNegativeInt })(
                <Input size="large" />
              )}
            </Form.Item>
            <Form.Item label="Order Quantity">
              {getFieldDecorator('qm', { rules: nonNegativeInt })(
                <Input size="large" />
              )}
            </Form.Item>
            <Form.Item>
              <Button size="large" block type="primary" htmlType="submit">
                Submit
              </Button>
            </Form.Item>
          </Form>
        )}
        {tpd.role === ROLE.MANUFACTURER && tpd.info.phase === 3 && (
          <Result
            icon={<Icon type="loading" style={{ fontSize: 72 }} spin />}
            title="Hold on"
            subTitle="Retailer is deciding the order quantity and retail price."
          />
        )}
        {tpd.info.phase === 4 && (
          <Button
            size="large"
            block
            type="primary"
            disabled={tpd.done}
            onClick={() => {
              confirm({
                title:
                  'You will not be able to see the information after this round.',
                onOk() {
                  tpd.socket.emit('setDone');
                }
              });
            }}
          >
            {tpd.done
              ? 'Waiting for other participants'
              : 'I have seen the information above'}
          </Button>
        )}
      </Layout>
    </React.Fragment>
  );
};

export default Form.create({ name: 'manufacturer' })(Manufacturer);
