import React from 'react';
import { PageHeader, Form, Icon, Input, Button, notification } from 'antd';

const Login = ({ tpd, form }) => {
  const { getFieldDecorator, validateFields } = form;

  const onFormSubmit = e => {
    e.preventDefault();
    validateFields((err, values) => {
      if (err) return;

      if (!tpd.socket) {
        return notification.open({
          message: '登入失敗',
          description: '伺服器連線失敗，請稍後再試'
        });
      }

      const { username, password } = values;
      const event = username === 'admin' ? 'loginAdmin' : 'login';
      tpd.socket.emit(event, { username, password }, error => {
        if (error) {
          notification.open({
            message: '登入失敗',
            description: error
          });
        }
      });
    });
  };

  return (
    <React.Fragment>
      <PageHeader title="Trade Promotion Decisions" />
      <Form onSubmit={onFormSubmit} style={{ padding: '12px 24px 24px 24px' }}>
        <Form.Item>
          {getFieldDecorator('username', {
            initialValue: 'admin',
            rules: [{ required: true, message: 'Please input your username!' }]
          })(
            <Input
              size="large"
              prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />}
              placeholder="Username"
            />
          )}
        </Form.Item>
        <Form.Item>
          {getFieldDecorator('password', {
            initialValue: '1234',
            rules: [{ required: true, message: 'Please input your Password!' }]
          })(
            <Input
              size="large"
              prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />}
              type="password"
              placeholder="Password"
            />
          )}
        </Form.Item>
        <Form.Item>
          <Button size="large" block type="primary" htmlType="submit">
            Log in
          </Button>
        </Form.Item>
      </Form>
    </React.Fragment>
  );
};

export default Form.create({ name: 'login' })(Login);
