import React, { Component } from 'react';
import { Layout, Modal } from 'antd';
import io from 'socket.io-client';
import Login from './Login';
import Admin from './Admin';
import Player from './Player';

import { ROLE } from '../util/const';

import './App.scss';

export default class App2 extends Component {
  state = {
    socket: null,
    game: {
      player: {
        max: 0,
        connected: 0,
        done: 0 // # of players have seen the information at stage 4
      },
      cond: null,
      param: {
        dtp: null,
        a: null,
        b: null,
        c: null
      },
      round: null
    },
    role: null,
    dual: null,
    done: false,
    info: {
      priority: null, // 由哪個角色決定促銷方式
      phase: null,
      promo: null,
      qr: null, // 市場
      pm: null,
      tp: null,
      qm: null,
      pr: null
    },
    profit: []
  };

  componentDidMount() {
    const socket = io(process.env.REACT_APP_BACKEND_URL);

    socket.on('connect', () => {
      this.setState({ socket });

      socket.on('disconnect', reason => {
        socket.close();
        Modal.info({
          title: 'Server Disconnected',
          content:
            'All transaction are still remained. Please click OK and login again.',
          onOk() {
            window.location.reload();
          }
        });
      });

      socket.on('updated', ({ role, dual, info, profit, done, game }) => {
        if (typeof game !== 'undefined') this.setState({ game });
        if (typeof role !== 'undefined') this.setState({ role });
        if (typeof dual !== 'undefined') this.setState({ dual });
        if (typeof info !== 'undefined') this.setState({ info });
        if (typeof profit !== 'undefined') this.setState({ profit });
        if (typeof done !== 'undefined') this.setState({ done });
      });
    });
  }

  render() {
    return (
      <Layout style={{ minHeight: '100vh' }}>
        <div className="container">
          {!this.state.role && <Login tpd={this.state} />}
          {this.state.role === ROLE.ADMIN && <Admin tpd={this.state} />}
          {(this.state.role === ROLE.MANUFACTURER ||
            this.state.role === ROLE.RETAILER) && <Player tpd={this.state} />}
        </div>
      </Layout>
    );
  }
}
