import validator from 'validator';

export const positiveInt = [
  {
    required: true,
    message: '必填'
  },
  {
    validator(rule, value, callback) {
      if (validator.isInt(value) && parseInt(value) > 0) {
        callback();
      }
      callback('請填正整數');
    }
  }
];

export const nonNegativeInt = [
  {
    required: true,
    message: '必填'
  },
  {
    validator(rule, value, callback) {
      if (validator.isInt(value) && parseInt(value) > 0) {
        callback();
      }
      callback('請填非負整數');
    }
  }
];
